import React, {useContext, useState, useEffect} from 'react'
import { Row, Col, Button, Input, Table,message,Card,Form,DatePicker,Select } from 'antd'
import { PlusOutlined } from '@ant-design/icons';
import { hospitalCol, schoolCol } from '../component/school/tableColumn/schoolColumn'
import AddHospital from '../component/hospital/modal/addHospital';
import { useQuery,useMutation } from '@apollo/client';
import { GET_ALL_HOSPITAL,ALL_HOSPIAL_INFO } from '../graphql/hospital';

import { DELETE_HOSPITALINFO_BY_ID } from '../graphql/hospital';
import CustomMap from '../component/covideComponents/CustomMap';
import AddSchool from '../component/school/modal/addSchool';
import { schooldashboardCol } from '../component/schooldashboard/tableColumn/schooldashboardColumn';
import { schoolLeftCol } from '../component/schooldashboard/tableColumn/schoolLeftColumn';
import SchoolBarChart from '../component/schooldashboard/chart/schoolBarChart';
import { qrcodeDashboardCol } from '../component/qrcodedashboard/tableColumn/qrcodeDashBoardColumn';
import moment from 'moment';
import ExportToExcel from '../component/export/exportToExcel';
import { PeopleList, QrCodeHeader, TotalQrCodeHeader } from '../asset/exportExcel/reportExcel';
import { DELETE_TRANSACTION_BY_ID, GET_DATA_TOTAL_BOXES, GET_MARKET_WITH_SCAN, GET_TRANSACTION_FOR_GRAPH, GET_TRANSACTION_WITH_PAGINATION } from '../graphql/qrcodedashboard';
import QrCodeBarChart from '../component/qrcodedashboard/chart/qrCodeBarChart';
import FilterQRGraph from '../component/qrcodedashboard/modal/filterQRGraph';
import { qrCodeLeftCol } from '../component/qrcodedashboard/tableColumn/qrcodeLeftColumn';

const {RangePicker} = DatePicker
const {Option} = Select

export default function QrcodeDashboard() {

    const [qrCodeData, setQRCodeData] = useState([])
    let [form] = Form.useForm()

    const [openFilterGraph,setOpenFilterGraph] = useState(false)
    const [chartData,setChartData] = useState([])

    const [boxData,setBoxData] = useState({})
    const [marketData,setMarketData] = useState([])

    const [marketName,setMarketName] = useState("")

    const [openAdd, setOpenAdd] = useState(false)
    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(10)
    const [keyword, setKeyword] = useState("")
    // const [peopleData, setPeopleData] = useState([])
    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })   

    const [filterData,setFilterData] = useState({
        keyword:"",
        createStartDate:null,
        createEndDate: null,
        marketName:"",
        limit:limit
    })

    var someDate = new Date();
    var numberOfDaysToAdd = 15;
    someDate.setDate(someDate.getDate() - numberOfDaysToAdd); 

    // alert(someDate)

    const [filterGraph,setFilterGraph] = useState({
        startDate:moment.parseZone(someDate),
        endDate:moment.parseZone(new Date()),
        marketName:"",
    })

    const {data:box,loading:boxLoading,refetch:refetchBoxes} = useQuery(GET_DATA_TOTAL_BOXES,{
        variables:{
            marketName:marketName
        },
        onCompleted:({getDataForTotalBoxes})=>{
            setBoxData(getDataForTotalBoxes)
        }
    })

    useEffect(()=>{
        refetchBoxes()
    },[marketName])

    const {data:market,loading:marketLoading} = useQuery(GET_MARKET_WITH_SCAN,{
        onCompleted:({getMarketWithTotalScan})=>{
            setMarketData(getMarketWithTotalScan)
        }
    })

    const {data,loading,error,refetch} = useQuery(GET_TRANSACTION_WITH_PAGINATION,{
        variables:{
            page: page,
            limit: filterData?.limit,
            keyword: filterData?.keyword,
            startDate:filterData?.createStartDate,
            endDate:filterData?.createEndDate,
            marketName:filterData?.marketName
        },
        onCompleted:({getTransactionWithPagination})=>{
            // console.log(getTransactionWithPagination)
            setQRCodeData(getTransactionWithPagination)
        },
        onError:(error)=>{
            message.error(error.message)
        },
        fetchPolicy:'network-only',
        nextFetchPolicy:'cache-only'
    })

    useEffect(()=>{
        if(data){
            setQRCodeData(data?.getTransactionWithPagination)
        }
    }, [data])

    const {data:graphData,loading:graphLoading,refetch:refetchGraph} = useQuery(GET_TRANSACTION_FOR_GRAPH,{
        variables:{
            startDate:filterGraph?.startDate,
            endDate:filterGraph?.endDate,
            marketName:filterGraph?.marketName
        },
        onCompleted:({getTransactionForGraph})=>{
            console.log(getTransactionForGraph)
            setChartData(getTransactionForGraph)
        },
        onError:(error)=>{
            message.error(error.message)
        },
        fetchPolicy:'network-only',
        nextFetchPolicy:'cache-only'
    })

    useEffect(()=>{
        if(graphData){
            setChartData(graphData?.getTransactionForGraph)
        }
    }, [graphData])

    const [deleteTrasaction, { loading: deleteLoading, error: deleteError }] = useMutation(DELETE_TRANSACTION_BY_ID, {     
        onCompleted: ({deleteTrasaction}) => {
            if(deleteTrasaction.success===true){
                refetch()
                message.success("")
            }else{
                message.error(deleteTrasaction.message)
            }
            
        },
        onError:(error)=>{
            message.error(error.message)
        }

    })

    const handleDelete =(e)=>{

        deleteTrasaction({
            variables:{
                transactionId:e
            }
        })
    }

    useEffect(() => {
        refetchGraph()
    }, [filterGraph])

    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = moment(e[0])
            let endDate = moment(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
        }else{
            setRanges({
                startDate: null,
                endDate: null
            })
        }
    }

    const dateHolder = ["", ""]

    const onFinish = (values) => {
        setFilterData({
            ...values,
            keyword:values?.keyword !==undefined ? values.keyword:"",
            marketName:values.marketName!== undefined ? values.marketName:"",
            createStartDate: values.createDate !==undefined && values.createDate !==null ? moment.parseZone(values.createDate[0]):null,
            createEndDate: values.createDate !==undefined && values.createDate !==null ? moment.parseZone(values.createDate[1]):null,
            limit:values.limit!==undefined ? limit:15
        })

    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    //get no for location data
    let qrCodeDataWithNo = [] 
    let dataToExcel = []
    qrCodeData?.transactions?.map((record,index) =>{
        let pageAdd = qrCodeData?.paginator?.currentPage > 1 ? ((qrCodeData?.paginator?.currentPage*limit)-limit)+1: 1;

        let setNoToqrCodeData = {...record,no:index+pageAdd}
        // console.log(record)
        if(record?.personalInfoId?.lastName===undefined){ 
            setNoToqrCodeData = {...setNoToqrCodeData,personalInfoId:{...record?.personalInfoId,firstName:"",lastName:"",id:null}}
        }
        qrCodeDataWithNo.push(setNoToqrCodeData)

        let excelData ={
            no:index+1,
            fullName: record?.personalInfoId?.lastName+" "+record?.personalInfoId?.firstName,
            tel: record?.personalInfoId?.tel,
            createdAt: moment(record?.createdAt).format("DD/MM/YYYY")
        }
        dataToExcel.push(excelData)

    })

    const showAll =()=>{
        form.resetFields()
        form.submit()
        
        // refetch()
    }

    const handleOpenFilter=()=>{
        setOpenFilterGraph(true)
    }

    const gridStyle = {
        width: '100%',
        textAlign: 'center',
      };

    return (
        <>
            <FilterQRGraph open={openFilterGraph} setOpen={setOpenFilterGraph} setFilterGraph={setFilterGraph} />
            <Row>
                <Col xs={24} md={24}>
                    <Row style={{height:'100%'}}>
                        <Col xs={24} md={5} style={{padding:10}}>
                            <Select
                                onChange={(e)=>setMarketName(e)}
                                placeholder=""
                                style={{width:'100%'}}
                            >
                                <Option value="">----</Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Col>
                    </Row>
                </Col>
                <Col xs={24} md={10} >
                    <Row style={{height:'100%'}}>
                        <Col xs={24} md={12} style={{padding:10}}>
                            <Card className="card-school" title={null} style={{height:'100%'}}>
                                <h2></h2>

                                <span style={{marginTop:10,fontSize:30}} >{boxData?.totalMarket}</span> <br/>
                                {/* <span className="school-badge-red">Content</span> */}
                            </Card>
                        </Col>
                        <Col xs={24} md={12} style={{padding:10}}>
                            <Card className="card-school" title={null} style={{height:'100%'}}>
                                <h2></h2>

                                <span style={{marginTop:10,fontSize:30}} >{boxData?.totalShops}</span> <br/>
                                <span className="school-badge-green"> {boxData?.totalShopToday}</span>
                            </Card>
                        </Col>
                        
                        <Col xs={24} md={12} style={{padding:10}}>
                            <Card className="card-school" title={null} style={{height:'100%'}}>
                                <h2></h2>

                                <span style={{marginTop:10,fontSize:30}} >{boxData?.totalBuyer}</span> <br/>
                                <span className="school-badge-green"> {boxData?.totalBuyerToday}</span>
                            </Card>
                        </Col>
                        <Col xs={24} md={12} style={{padding:10}}>
                            <Card className="card-school" title={null} style={{height:'100%'}}>
                                <h2></h2>

                                <span style={{marginTop:10,fontSize:30}} >{boxData?.totalTransaction}</span> <br/>
                                <span className="school-badge-green"> {boxData?.totalTransactionToday}</span>
                            </Card>
                        </Col>
                        
                    </Row>
                </Col>
                <Col xs={24} md={14} style={{padding:10}}>
                    <Card className="card-school" title={null} style={{height:'100%'}}>
                        <Button onClick={()=> handleOpenFilter()} >Filter</Button>
                        <QrCodeBarChart data={chartData} isBar="bar"  />
                    </Card>
                </Col>
            </Row>
            <Row>
                <Col xs={24} md={8}>
                    <Row>
                        <Col xs={24} md={24} >
                            <Row>
                                <Col xs={24} md={6} style={{padding:5}} >
                                    
                                    <Form.Item>
                                        <ExportToExcel 

                                            headers={TotalQrCodeHeader} 
                                            data={
                                                marketData
                                            } 
                                            filename={"MarketSummary"} 
                                            title={"Export"}
                                        />
                                    </Form.Item>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs={24} md={24} style={{padding:5}}>
                            <Table
                                // caseCol({handleDelete})
                                // style={{width:'100%'}}
                                className="school-table"
                                columns={qrCodeLeftCol({handleDelete,limit,page})}
                                dataSource={marketData}
                                rowKey={record => record.id}
                                pagination={false}
                                // pagination={{
                                //     total: schoolData?.paginator?.totalDocs,
                                //     //pageSizeOptions:["10", "20"],
                                //     // showSizeChanger: true,
                                //     current: schoolData?.paginator?.currentPage,
                                //     onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                                // }}
                                scroll={{ x: 400 }} 
                                sticky 
                            />
                        </Col>
                    </Row>
                </Col>
                <Col xs={24} md={16} >
                    <Row>
                
                        <Col xs={24} md={24} >
                        <Form
                        form={form}
                        name="search"
                        onFinish={onFinish}
                        onFinishFailed={onFinishFailed}
                        style={{width:'100%'}}
                    
                        >
                            <Row>
                                <Col xs={24} md={{span:4}} style={{padding:5}}>

                                    <Form.Item>
                                        <ExportToExcel 

                                            headers={QrCodeHeader} 
                                            data={
                                                dataToExcel
                                            } 
                                            filename={"QrCodeScanners"} 
                                            title={"Export to CSV"}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} md={{span:4}} style={{padding:5}}>
                                    <Form.Item name="limit">
                                        <Select placeholder="" style={{width:"100%"}} onChange={(e)=>setLimit(e)}>
                                            <Option value={15}>15</Option>
                                            <Option value={100}>100</Option>
                                            <Option value={200}>200</Option>
                                            <Option value={500}>500</Option>
                                            <Option value={1000}>1000</Option>
                                            <Option value={1000}>2000</Option>
                                            <Option value={1000}>5000</Option>
                                            <Option value={10000}>10000</Option>
                                        </Select>
                                    </Form.Item>

                                </Col>

                                <Col
                                    xs={24}
                                    md={{span:4}}
                                    style={{padding:5}}
                                >
                                    <Form.Item
                                        name="marketName"
                                        // rules={[{ required: true, message: '!' }]}
                                    >
                                        <Select
                                            // onChange={(e)=>setMarketName(e)}
                                            placeholder=""
                                            style={{width:'100%'}}
                                        >
                                            <Option value=""></Option>
                                            <Option value=""></Option>
                                        </Select>
                                    </Form.Item>
                                </Col>

                                <Col 
                                    xs={24}
                                    md={{span:4}}
                                    style={{padding:5}}
                                    >
                                    <Form.Item
                                        name="createDate"
                                        // rules={[{ required: true, message: '!' }]}
                                    >
                                        <RangePicker 
                                            // onChange={(e) => onSelectDate(e)}  
                                        
                                            placeholder={dateHolder}
                                            style={{width:"100%"}} 
                                        />
                                    </Form.Item>
                                </Col>

                                {/* <Col
                                    xs={24}
                                    md={{span:4}}
                                    style={{padding:5}}
                                >
                                    <Form.Item
                                        name="keyword"
                                        // rules={[{ required: true, message: '!' }]}
                                    >
                                    <Input.Search
                                        // onChange={(e)=>setKeyword(e.target.value)}
                                        placeholder=""
                                    />
                                    </Form.Item>
                                </Col> */}

                                <Col xs={24} md={{span:4}} style={{padding:5}}>
                                    <Form.Item >
                                        <Button htmlType="submit" type="primary" style={{ width: "100%" }}>
                                        
                                        </Button>
                                    </Form.Item>
                                    
                                </Col>
                                <Col xs={24} md={{span:4}} style={{padding:5}}>
                                    <Form.Item >
                                        <Button onClick={()=> showAll()} type="primary" style={{ width: "100%" }}>
                                            
                                        </Button>
                                    </Form.Item>
                                </Col>
                                
                            </Row>
                        </Form>
                        </Col>

                        <Col xs={24} md={24} style={{padding:5}}>
                            <Table
                                // caseCol({handleDelete})
                                // style={{width:'100%'}}
                                className="school-table"
                                columns={qrcodeDashboardCol({handleDelete})}
                                dataSource={qrCodeDataWithNo}
                                rowKey={record => record.id}
                                pagination={{
                                    total: qrCodeData?.paginator?.totalDocs,
                                    //pageSizeOptions:["10", "20"],
                                    // showSizeChanger: true,
                                    // current: schoolData?.paginator?.currentPage,
                                    pageSize:limit,
                                    onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                                }}
                                scroll={{ x: 900 }} 
                                sticky 
                                loading={loading}
                            />
                        </Col>
                    </Row>
                </Col>
            </Row>
            
        </>
    )
}
