        //     dataIndex: 'progress',
        //     key: 'progress',
        //     width: 100,
        //     render:(text,record)=>(
        //         <span>
        //             <Progress percent={record?.progress} status="active" strokeColor="rgb(244, 67, 54)" />
        //         </span>
        //     )
        // },
        
    
        // {
