import React, {useContext, useState,useEffect} from 'react'
import { Row, Col, Button, Input, Table, message,Select, DatePicker,Form, Checkbox } from 'antd'
import { PlusOutlined } from '@ant-design/icons';
import { PeopleController } from '../context/peopleContext';
import { peopleCol } from '../component/people/tableColumn/peopleColumn';
import AddPeople from '../component/people/modal/addPeople';
import { useQuery,useMutation } from '@apollo/client';
import { splitDot } from '../function/fn';
import { 
    DELETE_PERSONALINFO_BY_ID, 
    GET_ALL_PERSONINFO ,
    GET_ALL_PERSONINFO_FILTER,
    GET_ALL_PERSONINFO_SAMPLE
} from '../graphql/people';
import { DELETE_HOSPITALINFO_BY_ID } from '../graphql/hospital';
import { useLocation, useRouteMatch } from 'react-router-dom';
import moment from 'moment';
import ExportToExcel from "../component/export/exportToExcel";
import {PeopleList} from '../asset/exportExcel/reportExcel'
import {convertForPeopleExcel, convertPersonalToSelect, convertToCommune, convertToDistrict, convertToVillage, convertUserToSelect} from '../function/fn'
import { ListSelect, ListSelectTab } from '../static/own-comp';
import { provinceData, districtData, communeData, villageData } from '../context/headerContext'
import { ALL_USERS } from '../graphql/auth';
import { GET_BUYER_WITH_PAGINATION } from '../graphql/seller';
import { buyerPageCol } from '../component/buyer/tableColumn/buyerColumn';

const {Option} = Select
const {RangePicker} = DatePicker

export default function Buyer() {

    let [form] = Form.useForm()

    const [peopleData, setPeopleData] = useState({})
    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(15)
    const [keyword, setKeyword] = useState("")
    // const [peopleData, setPeopleData] = useState([])
    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })   

    const {data,loading,error,refetch} = useQuery(GET_BUYER_WITH_PAGINATION,{
        variables:{
            keyword:keyword,
            page:page,
            limit:limit,
            startDate:ranges?.startDate,
            endDate:ranges?.endDate
        },
        onCompleted:({getBuyerWithPagination})=>{
            setPeopleData(getBuyerWithPagination)
        },
        onError:(error)=>{
            message.error(error.message)
        },
        fetchPolicy:'network-only',
        nextFetchPolicy:"cache-only",
    })

    useEffect(()=>{
        if(data!==undefined){
            setPeopleData(data?.getBuyerWithPagination)
        }
    },[data])


    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = moment(e[0])
            let endDate = moment(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
        }else{
            setRanges({
                startDate: null,
                endDate: null
            })
        }
    }

    const dateHolder = ["", ""]

    const onFinish = (values) => {

    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    // get no for location data
    let peopleDataWithNo = []
    peopleData?.personalInfos?.map((record,index) =>{
        let pageAdd = peopleData?.paginator?.currentPage > 1 ? ((peopleData?.paginator?.currentPage*limit)-limit)+1: 1;

        let setNoToPeopleData = {...record,no:index+pageAdd}
        peopleDataWithNo.push(setNoToPeopleData)

    })

    return (
        <Row>
           
            <Form
                form={form}
                name="search"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                style={{width:'100%'}}
               
            >
                <Row>
                    <Col 
                        xs={24}
                        md={{span:4}}
                        style={{padding:5}}
                        >
                        <Form.Item
                            name="createDate"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <RangePicker 
                                onChange={(e) => onSelectDate(e)}  
                               
                                placeholder={dateHolder}
                                style={{width:"100%"}} 
                            />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span:4}}
                        style={{padding:5}}
                    >
                        <Form.Item
                            name="keyword"
                            // rules={[{ required: true, message: '!' }]}
                        >
                        <Input.Search
                            onChange={(e)=>setKeyword(e.target.value)}
                            placeholder=""
                        />
                        </Form.Item>
                    </Col>

                    {/* <Col xs={24} md={{span:4}} style={{padding:5}}>

                        <Form.Item>
                            <ExportToExcel 
                        
                                headers={PeopleList} 
                                data={
                                    dataToExcel
                                } 
                                filename={"sampleTest"} 
                                title={"Export to CSV"}
                            />
                        </Form.Item>

                        <Form.Item name="limit">
                            <Select placeholder="" style={{width:"100%"}} onChange={(e) => setLimit(e)}>
                                <Option value={15}>15</Option>
                                <Option value={100}>100</Option>
                                <Option value={200}>200</Option>
                                <Option value={500}>500</Option>
                                <Option value={1000}>1000</Option>
                                <Option value={1000}>2000</Option>
                                <Option value={1000}>5000</Option>
                                <Option value={1000}>10000</Option>
                            </Select>
                        </Form.Item>
                        
                    </Col> */}

                    {/* <Col xs={24} md={{span:4}} style={{padding:5}}>
                        <Form.Item >
                            <Button htmlType="submit" type="primary" style={{ width: "100%" }}>
                            
                            </Button>
                        </Form.Item>
                        
                    </Col>
                    <Col xs={24} md={{span:4}} style={{padding:5}}>
                        <Form.Item >
                            <Button onClick={()=> showAll()} type="primary" style={{ width: "100%" }}>
                                
                            </Button>
                        </Form.Item>
                    </Col> */}
                    
                </Row>
            </Form>
            <Col
                xs={24}
                style={{ marginTop: 20 }}
            >
                <Table
                    className="table-go-list"
                    columns={buyerPageCol()}
                    dataSource={peopleDataWithNo}
                    rowKey={record => record.id}
                    pagination={{
                        total: peopleData?.paginator?.totalDocs,
                        //pageSizeOptions:["10", "20"],
                        // showSizeChanger: true,
                        pageSize:limit,
                        // current: getPersonalInfoWithPagination?.paginator?.currentPage,
                        onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                    }}
                    scroll={{ x: 1000 }} 
                    sticky 
                    loading={loading}
                    
                />
            </Col>
        </Row>
    )
}
