import {gql} from '@apollo/client'

export const GET_TRANSACTION_WITH_PAGINATION = gql`
query getTransactionWithPagination($startDate:DateTime,$endDate:DateTime,$limit:Int,$page:Int,$marketName:String,$keyword:String,$shopName:String){
    getTransactionWithPagination(startDate:$startDate,endDate:$endDate,limit:$limit,marketName:$marketName,page:$page,keyword:$keyword,shopName:$shopName){
        transactions{
            shopId{
                _id
                name
                marketName
            }
            personalInfoId{
                id
                firstName
                lastName
                tel
            }
            _id
            createdAt
        }
        paginator{
            slNo
            prev
            next
            perPage
            totalPosts
            totalPages
            currentPage
            hasPrevPage
            hasNextPage
            totalDocs
        }
    }
}
`

export const DELETE_TRANSACTION_BY_ID = gql`
mutation deleteTrasaction($transactionId:ID!){
  deleteTrasaction(transactionId:$transactionId){
    success
    message
  }
}

`

export const GET_TRANSACTION_FOR_GRAPH = gql`
query getTransactionForGraph($startDate:DateTime,$endDate:DateTime, $marketName:String){
    getTransactionForGraph(startDate:$startDate,endDate:$endDate, marketName:$marketName){
        _id
        total
    }
}
`

export const GET_DATA_TOTAL_BOXES = gql`
query getDataForTotalBoxes($marketName:String){
  getDataForTotalBoxes(marketName:$marketName){
    totalTransaction
    totalTransactionToday
    totalBuyer
    totalBuyerToday
    totalShops
    totalShopToday
    totalMarket
  }
}

`

export const GET_MARKET_WITH_SCAN = gql`
{
  getMarketWithTotalScan{
    _id
    total
    today
    totalShop
    totalShoptoday
  }
}
`