import React, { useContext,useEffect, useState } from 'react'
import { Modal, Form, Input, Row, Col, Button, Select, message, Table, DatePicker } from 'antd';
import moment from 'moment';

const { Option } = Select;
const { RangePicker} = DatePicker

export default function FilterQRGraph({ open, setOpen,setFilterGraph , setRefetch }) {

    let [form] = Form.useForm()
    const dateHolder = ["", ""]

    useEffect(() => {
        
    }, [])

    const onFinish = (values) => {

        // if(values?.marketName=== undefined && values?.date===undefined){
        //     message.info("!")
        //     return
        // }

        var someDate = new Date();
        var numberOfDaysToAdd = 15;
        someDate.setDate(someDate.getDate() - numberOfDaysToAdd); 

        setFilterGraph({
            ...values,
            // keyword:values.keyword !==undefined ? values.keyword:"",
            marketName:values.marketName!== undefined ? values.marketName:"",
            startDate: values.date !==undefined && values.date !==null ? moment.parseZone(values.date[0]):moment.parseZone(someDate),
            endDate: values.date !==undefined && values.date !==null ? moment.parseZone(values.date[1]):moment.parseZone(new Date()),
        })

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <Modal
            title="Filter Graph"
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="filterGraph"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={12} style={{padding:5}}>
                        <Form.Item
                            name="marketName"
                            // rules={[{ required: true, message: '!' }]}
                        >

                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                            
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={12} style={{padding:5}}>
                        <Form.Item
                            name="date"
                            // rules={[{ required: true, message: '!' }]}
                        >

                            <RangePicker placeholder={dateHolder} style={{ width: "100%" }} />
                              
                        </Form.Item>
                    </Col>
                    <Col xs={{span: 24}} style={{padding:5}}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        // onClick={()=> }
                        >
                            
                        </Button>
                    </Col>

                   
                </Row>
            </Form>
        </Modal>
    )
}
